<template>
<div class="item" v-if="notice" @click="viewNoticeDetail">
    <div>
        <span class="item-title" :ref="`noticeItem-${notice.artclSeq}`">
            <span class="content" :ref="`contentItem-${notice.artclSeq}`">{{notice.artclSbj}} </span>
            <span class="three-dot" :ref="`dot-${notice.artclSeq}`"> ...</span>
        </span>
    </div>
    <div class="item-header d-flex justify-content-between">
        <div class="time">{{noticeTime}}</div>
    </div>
</div>
</template>

<script>
import moment from 'moment'

export default {
    name: 'NoticeItem',
    components: {},
    data: function () {
        return {
            noticeTime: null,
        }
    },
    props: {
        notice: null
    },
    async created() {
        if (this.notice && this.notice.regDt) {
            this.noticeTime = moment(this.notice.regDt).format("YYYY.MM.DD HH:mm");
        }
    },
    mounted: function () {
        console.log(123, this.$refs[`contentItem-${this.notice.artclSeq}`], this.$refs[`contentItem-${this.notice.artclSeq}`].clientWidth)
        console.log(5555, this.$refs[`noticeItem-${this.notice.artclSeq}`], this.$refs[`contentItem-${this.notice.artclSeq}`].clientWidth)
        // if (this.$refs[`noticeItem-${this.notice.artclSeq}`].clientHeight <= 35) {
        //     this.$refs[`dot-${this.notice.artclSeq}`].style.display = 'none';
        // }
    },
    methods: {
        viewNoticeDetail() {
            if (this.notice.artclSeq) {
                this.$router.push(`/notice/${this.notice.artclSeq}`)
            }
        }
    }
}
</script>
