<template>
<div class="mobile-page notices-container">
    <MainLayout :title="$t('Common.Notice.Title')" :leftIcon="`LEFT_ARROW`" :leftAction="() => this.$router.go(-1)">
        <div class="padding-content">
            <InputSearch :search="fetchSeachNotices" />
            <div class="items-CY">
                <template v-for="(notice, index) in notices">
                    <NoticeItem :notice="notice" :key="index" />
                </template>
                <InfiniteLoading :isSearch="isSearch" :isError="isError" :isComplete="isComplete" :fetchData="fetchNotices" :classParent="'.items-CY'" />
            </div>
        </div>
    </MainLayout>
</div>
</template>

<script>
import MainLayout from '@/components/Common/Layout/MainLayout'
import NoticeItem from '@/components/Notice/NoticeItem'
import InputSearch from '@/components/Common/Input/InputSearch'
import InfiniteLoading from '@/components/Common/Loading/InfiniteLoading'
import {
    CODE_BBS
} from '@/constants'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_NOTICES,
    CREATE_NOTICES
} from '@/mutations/Notice.mutation'

export default {
    name: 'Notices',
    components: {
        MainLayout,
        NoticeItem,
        InputSearch,
        InfiniteLoading
    },
    data: function () {
        return {
            page: 0,
            isComplete: false,
            isError: false,
            isSearch: false,
            searchValue: ''
        }
    },
    computed: {
        notices() {
            return this.$store.state.Notice.notices;
        },
        totalNotice() {
            return this.$store.state.Notice.totalNotice;
        }
    },
    async created() {
        await this.$store.dispatch("CREATE_NOTICES");
    },
    methods: {
        async fetchSeachNotices(searchValue = '') {
            await this.resetData();
            this.searchValue = searchValue;
            this.isSearch = true;
        },

        async fetchNotices() {
            try {
                this.page += 1;
                let params = {
                    bbsCd: CODE_BBS.NOTICE,
                    pageNum: this.page + '',
                    searchValue: this.searchValue,
                }
                await this.$store.dispatch("FETCH_NOTICES", params);

                if (this.totalNotice == 0 || this.notices.length == this.totalNotice) {
                    this.isComplete = true;
                }
                this.isSearch = false;
            } catch (error) {
                this.$store.dispatch(SHOW_POPUP_BUTTON, error.message);
                this.isError = true;
            }
        },

        resetData() {
            this.$store.dispatch('CREATE_NOTICES')
            this.page = 0;
            this.isComplete = false;
            this.isError = false;
        }
    },

    beforeDestroy() {
        this.searchValue = '';
        this.resetData();
    }
}
</script>

<style lang="scss">
.notices-container {
    .input-search {
        background-color: #F5F5F5;
        position: absolute;
        width: 100%;
    }
}
</style>
